"use client";
import Image from "next/image";
import React from "react";

// A timeline event block
function TimelineEvent({ year, content, isLeft }) {
  return (
    <div style={{ position: "relative", textAlign: isLeft ? "left" : "right" }}>
      {/* Year Label */}
      <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
        <div
          style={{
            backgroundColor: "#047857",
            color: "white",
            padding: "10px 20px",
            borderRadius: "50px",
          }}
        >
          {year}
        </div>
      </div>

      {/* Timeline Content */}
      <div
        style={{
          marginTop: "16px",
          padding: "16px",
          backgroundColor: "#e5e7eb",
          borderRadius: "10px",
          maxWidth: "400px",
          marginLeft: isLeft ? "0" : "auto",
          marginRight: isLeft ? "auto" : "0",
        }}
      >
        {content}
      </div>
    </div>
  );
}

export default function AboutPage() {
  // Timeline data
  const timelineEvents = [
    {
      year: "2014",
      content:
        "After Vishal Garg’s first attempt to purchase his own dream home, he quickly realized that the homebuying process is unnecessarily broken...",
    },
    {
      year: "2015",
      content: "Better Mortgage funds its first mortgage loan entirely online (without a single phone call!).",
    },
    {
      year: "2016",
      content:
        "Better Mortgage becomes a Fannie Mae approved seller + servicer and establishes relationships with top mortgage investors.",
    },
    {
      year: "2017",
      content: "Better expands into the real estate market with Better Real Estate.",
    },
    {
      year: "2018",
      content: "Better Mortgage partners with Ally Bank to build Ally powered by Better.",
    },
    {
      year: "2019",
      content:
        "Better Mortgage launches its pilot partnership with American Express to deliver a seamless homebuying experience to AMEX customers.",
    },
    {
      year: "2021",
      content: "Better acquires Trussle — The UK’s most innovative online mortgage broker.",
    },
    {
      year: "2022",
      content: "Better Mortgage becomes the first fintech to fund $100B home loans entirely online.",
    },
    {
      year: "2023",
      content:
        "Better Mortgage launches One Day Mortgage¹: The first offering for customers to go from application to full mortgage Commitment Letter within 24 hours.",
    },
  ];

  return (
    <div style={{ maxWidth: "1200px", margin: "auto", padding: "0 16px" }}>
      {/* Mission Section */}
      <div style={{ padding: "80px 0", textAlign: "center" }}>
        <h2 style={{ color: "#047857", fontWeight: "500", marginBottom: "24px" }}>Our mission</h2>
        <h1
          style={{
            fontSize: "32px",
            fontWeight: "500",
            maxWidth: "800px",
            margin: "auto",
            lineHeight: "1.5",
          }}
        >
          We're making homeownership simpler, faster — and most importantly, more accessible for all Americans.
        </h1>
      </div>

      {/* Status Quo Section */}
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "1fr 1fr",
          gap: "32px",
          alignItems: "center",
          marginBottom: "80px",
        }}
      >
        <div>
          <h2 style={{ fontSize: "44px", fontWeight: "500", marginBottom: "24px" }}>
            The status quo is broken
          </h2>
          <p style={{ fontSize: "18px", color: "#4b5563", marginBottom: "24px" }}>
            The traditional processes around homeownership are opaque and stressful...
            It’s a system set up to benefit insiders — not you. Better.com CEO, Vishal Garg,
            set out to change that.
          </p>
          <button
            style={{
              backgroundColor: "#047857",
              color: "white",
              padding: "12px 24px",
              borderRadius: "6px",
              border: "none",
              cursor: "pointer",
            }}
          >
            Read Vishal's story
          </button>
        </div>

        {/* Responsive Video Embed */}
        <div style={{ position: "relative" }}>
          {/* 16:9 Aspect Ratio Container */}
          <div
            style={{
              position: "relative",
              backgroundColor: "#e5e7eb",
              borderRadius: "10px",
              width: "100%",
              paddingBottom: "56.25%", // 16:9 aspect ratio
              overflow: "hidden",
            }}
          >
            <iframe
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
                border: "none",
              }}
              src="https://www.youtube.com/embed/1KjYlLBM9j4"
              title="YouTube video player"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>

            {/* Custom Overlay Play Icon (optional) */}
            <div
              style={{
                position: "absolute",
                inset: 0,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                pointerEvents: "none", // allows clicks on the iframe
                zIndex: 1,
              }}
            >
              <div
                style={{
                  width: "64px",
                  height: "64px",
                  backgroundColor: "white",
                  borderRadius: "50%",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <div
                  style={{
                    width: 0,
                    height: 0,
                    borderTop: "8px solid transparent",
                    borderBottom: "8px solid transparent",
                    borderLeft: "12px solid #1f2937",
                    marginLeft: "4px",
                  }}
                ></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* How We're Changing Things */}
      <div
        style={{
          width: "100%",
          backgroundColor: "#047857",
          color: "white",
          padding: "80px 16px",
          margin: "-16px",
        }}
      >
        <div style={{ maxWidth: "800px", margin: "auto" }}>
          <h2 style={{ fontSize: "4rem", fontWeight: "500", marginBottom: "32px" }}>
            How we're changing things
          </h2>
          <p style={{ fontSize: "1.5rem", marginBottom: "24px" }}>
            Homeownership is a huge part of our economy...
            We’re using technology to make it faster and more efficient,
            and humans to help make it friendly and enjoyable.
          </p>
        </div>
      </div>

      {/* Backed By */}
      <div style={{ padding: "80px 0" }}>
        <h2
          style={{
            fontSize: "24px",
            fontWeight: "500",
            textAlign: "center",
            marginBottom: "48px",
          }}
        >
          Backed by
        </h2>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(4, 1fr)",
            gap: "32px",
            alignItems: "center",
            maxWidth: "800px",
            margin: "auto",
          }}
        >
          {["SoftBank", "Ally", "Citi", "Ping An Bank", "Goldman Sachs", "KPCB", "American Express"].map(
            (name, index) => (
              <Image
                key={index}
                src={`/api/placeholder/120/60`}
                alt={name}
                width={120}
                height={60}
              />
            )
          )}
        </div>
      </div>

      {/* Company Timeline */}
      <div style={{ padding: "80px 0" }}>
        <h2
          style={{
            fontSize: "24px",
            fontWeight: "500",
            textAlign: "center",
            marginBottom: "48px",
          }}
        >
          Company timeline
        </h2>
        <div style={{ position: "relative" }}>
          {/* Vertical line in center */}
          <div
            style={{
              position: "absolute",
              left: "50%",
              transform: "translateX(-50%)",
              height: "100%",
              width: "1px",
              backgroundColor: "#d1d5db",
            }}
          ></div>

          {/* Timeline Events */}
          <div style={{ display: "flex", flexDirection: "column", gap: "40px" }}>
            {timelineEvents.map((event, index) => (
              <TimelineEvent
                key={event.year}
                year={event.year}
                content={event.content}
                isLeft={index % 2 === 0}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
